import { Component, OnInit, Input } from '@angular/core';
import { Subscription }   from 'rxjs/Subscription';

/* Services */
import { SkillsService } from '../../../Services/Skills/skills.service';
import { SharedService } from '../../../Services/shared.service';
import { InvitationService } from '../../../Services/Invitation/invitation.service';

/* Animations */
import { slideLeft, slideRight } from '../../../Animations/animations';

@Component({
	selector: 'app-signup-skills',
	templateUrl: './signup-skills.component.html',
	styleUrls: [
		'./signup-skills.component.scss',
		'../../../../public/styles/signup.scss'
	],
	animations: [slideLeft, slideRight],
	providers: [SkillsService]
})
export class SignupSkillsComponent implements OnInit {

	@Input() user_id 	: number
	skills 				: Object[]
	categories 			: Object[]
	user_skills			: Object[] = []
	stateLeft 			: string
	stateRight 			: string
	currentCategory 	: string
	subscription 		: Subscription
	skillSearch			: string

	constructor(private SkillsService: SkillsService,
				private SharedService: SharedService,
				private InvitationService: InvitationService) {

		this.stateRight 	= 'out'
		this.subscription 	= SharedService.signupModal$.subscribe( res => {
			if (res['index'] === 3) {
				this.sendInvitationToGoogleContacts();
				this.getUserSkills();
				if (res['lastIndex'] < 3)
					this.stateRight = 'in'
				else
					this.stateLeft 	= 'in'
			} else if (res['index'] > 3) {
				this.stateLeft = 'out'
				this.stateRight = 'in'
			} else {
				this.stateRight = 'out'
				this.stateLeft 	= 'in'
			}
		});
	}

	ngOnInit() {
		this.initServices();
	}

	sendInvitationToGoogleContacts() {
		this.InvitationService.sendGoogleAuthInvitation().subscribe(res => {
			console.log(res);
		});
	}

	initServices() {
		this.getSkills();
		this.getSkillCategories();
	}

	getSkills() {
		this.SkillsService.getSkills().subscribe( res => {
			this.skills = res.skills
		});
	}

	getSkillCategories() {
		this.SkillsService.getSkillCategories().subscribe( res => {
			this.categories = res.categories
		});
	}

	getSkillsByCategories(category) {
		this.currentCategory = category.name;
		this.SkillsService.getSkillsByCategories({ id: category.id }).subscribe( res => {
			this.skills = res.skills
		})
	}

	getUserSkills() {
		this.SkillsService.getUserSkills(this.user_id).subscribe( res => {
			this.user_skills = res.skills
		});
	}

	addSkill(skill) {
		if (this.checkSkill(skill)) {
			this.SkillsService.addUserSkills(this.user_id, {skill_id: skill.id}).subscribe( res => {
				if (res.skills) {
					this.user_skills = res.skills
				}
			});
		}
	}

	removeSkill(index, id) {
		this.SkillsService.deleteUserSkills(this.user_id, {skill_id: id}).subscribe( res => {
			if (res.success)
				this.user_skills.splice(index, 1)
		});
	}

	checkSkill(skill) {
		if (this.user_skills.indexOf(skill) >= 0)
			return false
		else
			return true
	}

	saveSkills() {
		setTimeout( () => {
			this.slideModals(4);
		}, 500)
	}

	slideModals(index) {
		this.SharedService.setSignupModalIndex(index, 3);
		if (index < 3)
			this.stateRight = 'out'
		else
			this.stateLeft 	= 'out'
	}
}



