import { Component, OnInit, Input } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { Router } from '@angular/router';

/* Services */
import { ProfilesService } from '../../../Services/Profiles/profiles.service';
import { SharedService } from '../../../Services/shared.service';

/* Animations */
import { slideRight, slideLeft } from '../../../Animations/animations';

@Component({
	selector: 'app-signup-about',
	templateUrl: './signup-about.component.html',
	styleUrls: [
		'./signup-about.component.scss',
		'../../../../public/styles/signup.scss'
	],
	animations: [slideRight, slideLeft],
})
export class SignupAboutComponent implements OnInit {

	@Input() profile	: Object
	status 				: string = "to meet smart people"
	about 				: any
	stateRight 			: string
	stateLeft 			: string
	stateFinal 			: string
	subscription 		: Subscription

	constructor(private ProfilesService: ProfilesService,
              private SharedService: SharedService,
              private route: Router) {

    this.stateRight = 'out'
    this.stateFinal = 'out'
		this.subscription 	= SharedService.signupModal$.subscribe( res => {
			if (res['index'] === 5) {
				this.stateRight = 'in'
			}
		});
	}

	ngOnInit() {
		this.about = this.profile['description'];
	}

	selectStatus(status: string) {
		if (status === 'to share')
			this.status = "to share what I'm working on"
		else
			this.status = status
	}

	// getProfile() {
	// 	console.log(this.pipe.transform(this.profile['description']));

	// }

	updateProfile() {
		const body = {
			description	: this.replaceNewline(this.about || ''),
			about 		: this.status
		}

		this.ProfilesService.updateProfile(this.profile['uid'], body).subscribe( res => {
			if (res.success) {
				this.slideModals(6);
				this.stateLeft 	= 'out'
				this.stateFinal = 'in'
				setTimeout( () => {
					this.redirectToProfile();
				}, 2000);
			}
		});
	}

	redirectToProfile() {
    	this.route.navigate(['/', 'welcome']);
	}

	replaceNewline(text) {
		return text.replace(/\r?\n/g, '<br />');
	}

	slideModals(index) {
		this.stateRight = 'out'
		this.SharedService.setSignupModalIndex(index, 5);
	}

}



