import { Component, OnInit, Input, trigger, state, style, animate, transition, keyframes } from '@angular/core';
import { Subscription }   from 'rxjs/Subscription';

/* Services */
import { InterestsService } from '../../../Services/Interests/interests.service';
import { SharedService } from '../../../Services/shared.service'

/* Animations */
import { slideLeft, slideRight } from '../../../Animations/animations';

@Component({
	selector: 'app-signup-interests',
	templateUrl: './signup-interests.component.html',
	styleUrls: [
		'./signup-interests.component.scss',
		'../../../../public/styles/signup.scss'
	],
	animations: [slideLeft, slideRight],
	providers: [InterestsService]
})
export class SignupInterestsComponent implements OnInit {

	@Input() user_id 	: number
	interests 			: Object[]
	user_interests		: Object[] = []
	stateRight			: string
	stateLeft			: string
	subscription 		: Subscription
	interestSearch		: string

	constructor(private InterestsService: InterestsService,
				private SharedService: SharedService) {

		this.stateRight 	= 'out'
		this.subscription 	= SharedService.signupModal$.subscribe( res => {
			if (res['index'] === 4) {
				if (res['lastIndex'] < 4)
					this.stateRight = 'in'
				else
					this.stateLeft 	= 'in'
			} else if (res['index'] > 4) {
				this.stateLeft = 'out'
				this.stateRight = 'in'
			} else {
				this.stateRight = 'out'
				this.stateLeft 	= 'in'
			}
		});
	}

	ngOnInit() {
		this.initService();
	}

	initService() {
		this.getInterests();
	}

	getInterests() {
		this.InterestsService.getInterests().subscribe( res => {
			this.interests = res.interests
		});
	}

	addRemoveInterest(interest) {
		if (this.checkInterest(interest)) {
			this.InterestsService.addUserInterests(this.user_id, { name: interest }).subscribe( res => {
				if (res.interests)
					this.user_interests.push(interest);
			});
		} else {
			this.removeInterest(interest);
		}
	}

	removeInterest(interest) {
		this.InterestsService.deleteUserInterests(this.user_id, { name: interest }).subscribe( res => {
			if (res.interests)
				this.user_interests.splice(this.user_interests.indexOf(interest), 1)
		});
	}

	checkInterest(interest) {
		if (this.user_interests.indexOf(interest) >= 0)
			return false
		else
			return true
	}

	saveInterests() {
		setTimeout( () => {
			this.slideModals(5);
		}, 500);
	}

	slideModals(index) {
		this.SharedService.setSignupModalIndex(index, 4);
		if (index < 4)
			this.stateRight = 'out'
		else
			this.stateLeft 	= 'out'
	}
}



