import { Component, OnInit, Input } from '@angular/core';
import { DialogService } from "ng2-bootstrap-modal";
import { Subscription }   from 'rxjs/Subscription';

/* Component */
import { ExperienceModalComponent } from '../../modals/experience-modal/experience-modal.component';
import { LinkedInApiComponent } from '../../modals/api-modals/linked-in-api/linked-in-api.component';

/* Services */
import { ExperiencesService } from '../../../Services/Experiences/experiences.service';
import { SharedService } from '../../../Services/shared.service'

/* Animations */
import { slideLeft, slideRight } from '../../../Animations/animations';

@Component({
	selector: 'app-signup-experience',
	templateUrl: './signup-experience.component.html',
	styleUrls: [
		'./signup-experience.component.scss',
		'../../../../public/styles/signup.scss'
	],
	animations: [slideLeft, slideRight],
	providers: [ExperiencesService]
})
export class SignupExperienceComponent implements OnInit {

	@Input() user_id 	: number
	stateLeft 			: string
	stateRight 			: string
	experiences 		: Object[]
	subscription 		: Subscription

	constructor(private dialogService: DialogService,
				private ExperiencesService: ExperiencesService,
				private SharedService: SharedService) {

		this.stateRight 	= 'out'
		this.subscription 	= SharedService.signupModal$.subscribe( res => {
			if (res['index'] === 2) {
				if (res['lastIndex'] === 1)
					this.stateRight = 'in'
				else
					this.stateLeft 	= 'in'
			} else if (res['index'] > 2) {
				this.stateLeft = 'out'
				this.stateRight = 'in'
			} else {
				this.stateRight = 'out'
				this.stateLeft 	= 'in'
			}
		});
	}

	ngOnInit() {
		this.initModels();
		this.getUserExperiences();
	}

	initModels() {
		this.experiences 	= [];
	}

	getUserExperiences() {
		this.ExperiencesService.getUserExperiences(this.user_id).subscribe( res => {
			this.experiences = res.experiences
		});
	}

	removeUserExperience(id, index) {
		if (this.user_id) {
			this.ExperiencesService.deleteExperience(this.user_id, {id: id})
			.subscribe( res => {
				if (res.success)
					this.experiences.splice(index, 1);
			});
		}
	}

	showExperienceModal(index) {
		if (this.user_id) {
    		const object = index < 0 ? {} : this.experiences[index]
	        let disposable = this.dialogService.addDialog(ExperienceModalComponent, {
	        	index 			 	: index,
	            user_experience 	: object
	        })
	        .subscribe((success) => {
	        	if (success)
	        		this.getUserExperiences();
	        })
	    }
	}

	openLinkedInApiModal() {
		let disposable 	= this.dialogService.addDialog(LinkedInApiComponent, {
        	profile 	: {},
        	index 		: 0
    	}).subscribe( success => {
    		if (success)
    			this.getUserExperiences();
    	});
	}

	saveExperience() {
		setTimeout( () => {
			this.slideModals(3);
		}, 1000)
	}

	slideModals(index) {
		this.SharedService.setSignupModalIndex(index, 2);
		if (index === 1)
			this.stateRight = 'out'
		else
			this.stateLeft 	= 'out'
	}
}



