import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';
import { PipeModule } from '../../Pipes/pipe.module';
import { FooterModule } from '../footer/footer.module';
import { ModalModule } from '../modals/modal.module';

/* Components */
import { SignupComponent } from './signup.component';
import { SignupExperienceComponent } from './signup-experience/signup-experience.component';
import { SignupAboutComponent } from './signup-about/signup-about.component';
import { SignupSkillsComponent } from './signup-skills/signup-skills.component';
import { SignupInterestsComponent } from './signup-interests/signup-interests.component';

/* Services */
import { InvitationService } from '../../Services/Invitation/invitation.service';

/* Directives */
import { DirectivesModule } from '../../Directives/directives.module';

/* Librairies */
import { MomentModule } from 'angular2-moment';
import { Ng2CloudinaryModule } from 'ng2-cloudinary';
import { FileUploadModule } from 'ng2-file-upload';

/* Routes */
import { SIGNUP_ROUTES } from '../../Routes/signup.routes';

@NgModule({
	imports: [
		SIGNUP_ROUTES,
		CommonModule,
		FormsModule,
		RouterModule,
		PipeModule,
		FooterModule,
		ModalModule,
		DirectivesModule,
		MomentModule,
		Ng2CloudinaryModule,
		FileUploadModule
	],
	declarations: [
		SignupComponent,
		SignupExperienceComponent,
		SignupAboutComponent,
		SignupSkillsComponent,
		SignupInterestsComponent,
	],
	entryComponents: [
	],
	providers: [ InvitationService ]
})
export class SignupModule { }



