import { Component, OnInit } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { Router, ActivatedRoute } from '@angular/router';
import { CloudinaryOptions, CloudinaryUploader } from 'ng2-cloudinary';

/* Services */
import { TokenService } from '../../Services/Authentication/token.service';
import { ProfilesService } from '../../Services/Profiles/profiles.service';
import { SharedService } from '../../Services/shared.service';
import { NetworksService } from '../../Services/Networks/networks.service';
import { PicturesService } from '../../Services/Pictures/pictures.service';
import { BackofficeService } from '../../Services/Backoffice/backoffice.service';

/* Interfaces */
import { networks } from '../../Interfaces/Constants/networks-constant';

/* Animations */
import { slideLeft } from '../../Animations/animations';
import { fadeInWord } from '../../Animations/fadeInWord.animation';

export const fadeInObject = [
	'fadeOut',
	'fadeOut',
	'fadeOut',
	'fadeOut',
	'fadeOut'
]

@Component({
	selector: 'app-signup',
	templateUrl: './signup.component.html',
	styleUrls: [
		'./signup.component.scss',
		'../../../public/styles/signup.scss'
	],
	animations: [slideLeft, fadeInWord],
	providers: [ProfilesService, NetworksService, BackofficeService]
})
export class SignupComponent implements OnInit {

	my_id 			: Object
	sexe 			: string = 'Male'
	stateLeft 		: string
	profile_picture : string
	searchNetwork 	: string
	emailNetwork	: string
	email 			: string
	location 		: any
	loadNetwork		: boolean = false
	check 			: boolean = false
	uploading 		: boolean = false
	showNetwork 	: boolean = false
	verification 	: boolean = false
	verification2 	: boolean = false
	mobile 			: boolean = false
	reloadChildren 	: boolean = false
	subscription 	: Subscription
	profile 		: Object
	networks 		: Object[]
	startupNetwork  : Array<string> = networks
	long 			: number
	lat 			: number
	fadeInState 	: Array<string> = fadeInObject

	uploader: CloudinaryUploader = new CloudinaryUploader(
		new CloudinaryOptions({ cloudName: 'dqpkpmrgk', uploadPreset: 'z7rzegb5', autoUpload: true })
	);

	constructor(private TokenService: TokenService,
				private SharedService: SharedService,
				private ProfilesService: ProfilesService,
				private NetworksService: NetworksService,
				private PicturesService: PicturesService,
				private route: ActivatedRoute,
				private router: Router,
				private BackofficeService: BackofficeService) {

		this.route.queryParams.subscribe(params => {
			if (params['id'])
				this.initConstructor();
			else
				this.router.navigate(['/login']);
		});

		this.imageUpload();
	}

	ngOnInit() {
		this.initWindow();
    	this.initCookie();
		this.getNetworks();
		this.fadeInState[0] = 'fadeIn'
	}

	initConstructor() {
		this.getToken();
		this.stateLeft		= 'in'
		this.subscription 	= this.SharedService.signupModal$.subscribe( res => {

			let fadeIndex = this.fadeInState.indexOf('fadeIn');

			this.fadeInState[fadeIndex] 		= 'fadeOut'
			this.fadeInState[res['index'] - 1] 	= 'fadeIn'

			if (res['index'] === 1) {
				this.stateLeft = 'in'
			}
		});
	}


	initWindow() {
		window.scrollTo(0, 0);

		if ((window.screen.width) < 736)
			this.mobile = true
	}

	initCookie() {
		if (localStorage.getItem('network')) {
			this.searchNetwork = localStorage.getItem('network');
			this.loadNetwork = true
			this.updateProfileNetwork(this.searchNetwork);
		}
		if (localStorage.getItem('project')) {
			const project_token = localStorage.getItem('project');
			this.BackofficeService.updateProjectCreator(project_token, {}).subscribe( res => {
				console.log(res);
			});
		}
	}

	imageUpload() {
		this.uploader.onSuccessItem = (item: any, response: string, status: number, headers: any) => {
			this.uploading 			= false
			this.profile_picture 	= JSON.parse(response)['secure_url'];
		};

		this.uploader.onAfterAddingFile = (fileItem) => {
			this.uploading = true
			this.uploader.uploadAll();
		};
	}

	setDefaultPicture(index, id) {
		this.PicturesService.setDefaultProfilePicture(index, id).subscribe( res => {
			this.reloadHeader();
		});
	}

	getToken() {
		if (this.TokenService.getToken()) {
			this.my_id = this.TokenService.getToken().user
			this.getProfile(this.my_id['id']);
		}
		// } else
		// 	this.router.navigate(['/login']);
	}

	getNetworks() {
		this.NetworksService.getNetworks('university').subscribe( res => {
			this.networks = res.university
		});
	}

	getProfile(id) {
		this.ProfilesService.getProfile(id).subscribe( res => {
			this.profile = res.profile
			if (!this.profile['picture']) {
				this.setDefaultPicture(0, id);
			}
			else {
				this.setDefaultPicture(1, id);
				this.profile_picture = this.profile['picture']
			}
		});
	}

	getSexe(sexe: string) {
		this.sexe = sexe
	}

	getAddress(place: Object) {
		const address 	= place['formatted_address'].replace('USA', 'United States');
		this.location 	= address.split(', ');
		this.long 		= place['geometry'].location.lat();
		this.lat 		= place['geometry'].location.lng();
		this.check 		= true
	}

	checkEmail(email) {
		const reg = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
		return reg.test(email)
	}

	updateProfileNetwork(network) {
		const body = {
			network: network
		}
		this.ProfilesService.updateProfile(this.my_id['id'], body).subscribe(res => {
			console.log(res);
		});
	}

	updateBasic() {
		if (this.location && this.location[0]) {
			const body = {
				genre 			: this.sexe,
				picture 		: this.profile_picture,
				location 		: {
					city 			: this.location[0],
					state 			: this.location[1],
					country 		: this.location[this.location.length - 1],
					lng 			: this.long,
					lat 			: this.lat
				}
			}
      if (!this.loadNetwork && this.checkEmail(this.email) && this.searchNetwork) {
			this.NetworksService.joinNetwork({ email: this.email, network: this.searchNetwork })
				.subscribe( res => {
					if (res.success) {
						this.email = ''
						this.searchNetwork = ''
					}
				});
			}
			this.ProfilesService.updateProfile(this.my_id['id'], body).subscribe( res => {
				if (res.success) {
					this.reloadHeader();
					setTimeout( () => {
						this.slideModals(2);
					}, 500);
				}
			});
		}
	}

	onFocus(event) {
		this.showNetwork = this.showNetwork ? false : true
	}

	onNetworkChange(event) {
		if (!event)
			this.verification = this.verification2 = false
	}

	networkVerification(name, website) {
		this.verification = this.verification2 = false

		if (this.startupNetwork.indexOf(name) >= 0)
			this.verification2 	= true
		else {
			this.getEmailNetwork(website);
			this.verification 	= true
		}
	}

	getEmailNetwork(website) {
		const position1 = website.indexOf('.') + 1
		const position2 = website.lastIndexOf('.');
		this.emailNetwork = 'email@' + website.slice(position1, position2) + '.edu'
	}

	selectNetwork(name, website) {
		this.searchNetwork = name
		this.networkVerification(name, website);
	}

	slideModals(index) {
		if (this.check) {
			this.stateLeft = 'out'
			this.SharedService.setSignupModalIndex(index, 1);
		}
	}

	reloadHeader() {
		this.SharedService.reloadHeader(true);
	}

}



